/* @flow */

import React from 'react';
import styled from 'styled-components';
import {Parallax} from 'react-parallax';
import Link from '../Link';
import mainImage from './meditation-2214532.jpg';

const Container = styled.div`
  height: 400px;
  width: 100%;
`;

const StyledParallax = styled(Parallax)`
  height: 400px;
`;

const StyledTitle = styled.h1`
  margin: 15px;
  color: #e72f34;
  background: rgba(54, 44, 42, 0.95);
  padding: 10px;
  display: inline-block;
`;

const MainCTA = styled(Link)`
  display: inline-block;
  background: rgb(252, 154, 29);
  left: 262px;
  position: absolute;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  border: 4px solid #362c2a;
  color: #362c2a;
  transition: 0.3s all;

  &:hover {
    background: #e72f34;
    left: 270px;
    letter-spacing: 1.2px;
  }
`;

type Props = {};

class Component extends React.Component<Props> {
  render() {
    return (
      <Container>
        <StyledParallax bgImage={mainImage} strength={600}>
          <br/>
          <StyledTitle>Welcome to the school of free Reiki</StyledTitle>
          <br/>
          <MainCTA href="/free_reiki_courses_11.html">Learn Reiki Level 1 for free</MainCTA>
        </StyledParallax>
      </Container>
    );
  }
}

export default Component;



